import * as cot from '../constants/collect';

const collectInfo = (state = {username: "",collects: []},action) => {
    switch(action.type) {
        case cot.ADD : 
            state.username = action.username;
            state.collects.push({shopId: action.shopId})
            return state; break;
        case cot.DELETE : 
            console.log(action);
            let colIndex = 0;
            state.collects.map((item,index) => {
                if(item.shopId == action.shopId) {
                    colIndex = index;
                    return;
                }
            })
            console.log("colIndex = " + colIndex);
            state.collects.splice(colIndex,1);
            return state;
        default: return state;break;
    }
}


export {collectInfo}


